<template>
  <div>
    <el-row>
      <header-layout class="header-layout" :head_title="'Physical Modeling'"></header-layout>
    </el-row>


    <el-card
      class="box-card"
    >
    <!-- <div class="department-outer"> -->
      <div class="view-box" v-if="showTree">
        <OrgView
          @on-node-click="handleNodeClick"
          :data="datas"
          :zoom-handled="0.9"
          @on_button_click="handleButtonClick"
          ref="myOrgView"
          v-if="hackReset"
        ></OrgView>
      </div>
      <PhyTable
        v-model="datas"
        :selectedNode="selectedNode"
        v-if="!showTree"
        @go-back="goBack"
        @on_button_click="handleButtonClick"
      ></PhyTable>
    <!-- </div> -->

    </el-card>

    <!-- Dialogs -->
    <!-- 添加 -->
    <el-dialog
      title="添加信息"
      :visible.sync="dialogVisible_add"
      width="30%"
      :before-close="handleClose"
    >
      <el-form ref="form" :model="addForm" label-width="80px">
        <el-form-item label="结点名称: ">
          <el-input v-model="addForm.label"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible_add = false">取 消</el-button>
        <el-button type="primary" @click="addNode">确 定</el-button>
      </span>
    </el-dialog>

    <!-- 删除 -->
    <el-dialog
      title="删除信息"
      :visible.sync="dialogVisible_delete"
      width="30%"
      :before-close="handleClose"
    >
      <span>确定要删除{{selectedNode==null?"":selectedNode.label}}吗？</span>

      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible_delete = false">取 消</el-button>
        <el-button type="primary" @click="deleteNode">确 定</el-button>
      </span>
    </el-dialog>

    <!-- 编辑 -->
    <el-dialog
      title="编辑信息"
      :visible.sync="dialogVisible_edit"
      width="30%"
      :before-close="handleClose"
    >
      <el-form ref="form" :model="editForm" label-width="80px">
        <el-form-item label="结点名称: ">
          <el-input v-model="editForm.label"></el-input>
        </el-form-item>
      </el-form>

      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible_edit = false">取 消</el-button>
        <el-button type="primary" @click="editNode">确 定</el-button>
      </span>
    </el-dialog>

    <!-- 详细信息 -->
    <el-dialog
      title="详细信息"
      :visible.sync="dialogVisible_detail"
      width="30%"
      :before-close="handleClose"
    >
      <span>{{selectedNode==null?"":selectedNode.label}}</span>

      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible_detail = false">取 消</el-button>
        <el-button type="primary" @click="dialogVisible_detail = false">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
// import OrgView from './org-tree/components/org-view.vue'
// const menuList = ["add", "delete", "edit", "detail"];

export default {
  name: "PhysicalModeling",
  components: {
    OrgView: () => import("./org-tree/components/org-view.vue"),
    PhyTable: () => import("./PhyTable.vue"),
     HeaderLayout: () => import('@/pages/contents/system_configuration/SysComponents/HeaderLayout')
  },
  data() {
    return {
      changeLog: "updated by Bai Xiaoran,2019/03/15,11:00AM",
      showTree: true,
      datas: {
        id: 0,
        label: "XXX科技",
        level: 1,
        children: [
          {
            id: 2,
            label: "产品研发部",
            level: 2,
            children: [
              {
                id: 5,
                label: "研发-前端",
                level: 3,
                children: []
              },
              {
                id: 6,
                label: "研发-后端",
                level: 3,
                children: []
              },
              {
                id: 9,
                label: "UI设计",
                level: 3,
                children: [
                  {
                    id: 12,
                    label: "第四层",
                    level: 4,
                    children: []
                  },
                  {
                    id: 13,
                    label: "第四层",
                    level: 4,
                    children: []
                  },
                  {
                    id: 14,
                    label: "第四层",
                    level: 4,
                    children: []
                  },
                  {
                    id: 15,
                    label: "第四层",
                    level: 4,
                    children: []
                  }
                ]
              },
              {
                id: 10,
                label: "产品经理",
                level: 3,
                children: []
              },
              {
                id: 20,
                label: "产品经理",
                level: 3,
                children: [
                  {
                    id: 16,
                    label: "第四层",
                    level: 4,
                    children: []
                  },
                  {
                    id: 17,
                    label: "第四层",
                    level: 4,
                    children: []
                  }
                ]
              }
            ]
          },
          {
            id: 3,
            label: "销售部",
            level: 2,
            children: [
              {
                id: 7,
                label: "销售一部",
                level: 3,
                children: []
              },
              {
                id: 8,
                label: "销售二部",
                level: 3,
                children: [
                  {
                    id: 18,
                    label: "第四层",
                    level: 4,
                    children: []
                  },
                  {
                    id: 19,
                    label: "第四层",
                    level: 4,
                    children: []
                  }
                ]
              }
            ]
          },
          {
            id: 4,
            label: "财务部",
            level: 2,
            children: []
          },
          {
            id: 11,
            label: "HR人事",
            level: 2,
            children: []
          }
        ]
      },
      dialogVisible_add: false,
      dialogVisible_delete: false,
      dialogVisible_edit: false,
      dialogVisible_detail: false,
      selectedNode: {
        label: ""
      },
      addForm: { label: "" },
      editForm: { label: "" },
      detailForm: {
        label: ""
      },
      maxNodeId: 0,
      hackReset: true
    };
  },
  computed: {},
  methods: {
    goBack() {
      this.showTree = true;
    },
    doExport() {
      console.log(this.datas);
      this.datas.label = "test";
    },
    _mapData(data, fn) {
      fn(data);
      const children = data.children;
      if (children) {
        children.forEach(child => {
          this._mapData(child, fn);
        });
      }
    },
    updataMaxNodeId() {
      this._mapData(this.datas, data => {
        this.maxNodeId = data.id > this.maxNodeId ? data.id : this.maxNodeId;
      });
    },
    setDepartmentData(data) {
      data.isRoot = true;
      return data;
    },
    handleNodeClick(e, data, expand) {
      this.selectedNode = data;
      this.showTree = false;
    },
    getDepartmentData() {},
    handleButtonClick({ key, data }) {
      // this.$message({
      //   message: `点击了《${data.label}》节点的'${key}'菜单`
      // });
      if (key == "add") {
        this.dialogVisible_add = true;
        this.addForm.label = "";
      } else if (key == "delete") {
        this.dialogVisible_delete = true;
      } else if (key == "edit") {
        this.dialogVisible_edit = true;
        this.editForm.label = data.label;
      } else {
        this.dialogVisible_detail = true;
      }
      this.selectedNode = data;
    },
    handleClose(done) {
      this.$confirm("确认关闭？")
        .then(_ => {
          done();
        })
        .catch(_ => {});
    },
    addNode() {
      this.updataMaxNodeId();
      this.dialogVisible_add = false;
      var flag = false;

      const newNode = {
        id: this.maxNodeId + 1,
        label: this.addForm.label,
        level: this.selectedNode.level + 1,
        children: new Array()
      };

      // console.log(this.selectedNode)
      this._mapData(this.datas, _data => {
        if (_data.id == this.selectedNode.id) {
          // console.log("selected!", _data.children)
          var _children = _data.children;
          if (
            _children === undefined ||
            _children === null ||
            _children === []
          ) {
            // console.log("enter！")
            _data.children = Array();
            flag = true;
          }
          _data.children.push(newNode);
        }
      });

      if (this.selectedNode.level == 3 && !this.selectedNode.expand) {
        this.$refs.myOrgView.$refs.myOrgTree.handleExpand(this.selectedNode);
      }

      // console.log(this.datas)
    },
    deleteNode() {
      this.dialogVisible_delete = false;
      this._mapData(this.datas, data => {
        if (data.children != null) {
          var _children = data.children;
          for (var i = 0; i < data.children.length; i++) {
            if (_children[i].id == this.selectedNode.id) {
              _children.splice(i, 1);
            }
          }
        }
        // this.hackReset = false;
        // this.$nextTick(() => {
        //   this.hackReset = true;
        // });
      });
    },
    editNode() {
      this.dialogVisible_edit = false;
      this._mapData(this.datas, _data => {
        if (_data.id == this.selectedNode.id) {
          _data.label = this.editForm.label;
        }
      });
    },
    detailNode() {}
  },
  mounted() {
    this.getDepartmentData();
    // this.store.sys.state.datas = this.datas;
  }
};
</script>

<style scoped>
@import "org-tree/index.css";
.box-card{
  /* overflow-x: auto; */
  /* overflow-y: hidden; */
  height: 100%;
  width: 100%;
}
.title {
  font-size: 25px;
}
.import-export-button {
  float: right;
  margin-right: 40px;
}
.container {
  padding: 20px 20px;
  /* text-align: center; */
  /* margin-left: 100px;
  margin-top: 50px; */
  /* height: 500px; */
  /* background-color: aqua */
}

.changeLog {
  width: 100%;
  height: 35px;
}
.changeLog div {
  margin-top: 10px;
  position: relative;
  float: right;
  margin-right: 40px;
}
.changeLog label {
  font-size: 16px;
}
.graph {
  margin-top: 10px;
  width: 100%;
  /* height: 100%; */
  background-color: #fff;
  border: #c0c0c0 solid 1px;
  border-radius: 5px;
}
.percent-100 {
  width: 100%;
  height: 100%;
}
.department-outer {
  width: 100%;
  height: 100%;
  overflow: hidden;
}
.department-outer .tip-box {
  position: absolute;
  left: 20px;
  top: 20px;
  z-index: 12;
}
.department-outer .zoom-box {
  position: absolute;
  right: 30px;
  bottom: 30px;
  z-index: 2;
}
.department-outer .view-box {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 1;
  cursor: move;
}
.department-outer .view-box .org-tree-drag-wrapper {
  width: 100%;
  height: 100%;
}
.department-outer .view-box .org-tree-wrapper {
  display: inline-block;
  position: fixed;
  left: 50%;
  top: 50%;
  transition: transform 0.2s ease-out;
  margin-left: 0%;
}
.department-outer .view-box .org-tree-wrapper .org-tree-node-label {
  box-shadow: 0px 2px 12px 0px rgba(143, 154, 165, 0.4);
  border-radius: 4px;
}
.department-outer
  .view-box
  .org-tree-wrapper
  .org-tree-node-label
  .org-tree-node-label-inner {
  padding: 0;
}
.department-outer
  .view-box
  .org-tree-wrapper
  .org-tree-node-label
  .org-tree-node-label-inner
  .custom-org-node {
  padding: 14px 41px;
  user-select: none;
  word-wrap: none;
  white-space: nowrap;
  border-radius: 4px;
  color: #000000;
  font-size: 14px;
  font-weight: 500;
  line-height: 20px;
  cursor: default;
}
.department-outer
  .view-box
  .org-tree-wrapper
  .org-tree-node-label
  .org-tree-node-label-inner
  .custom-org-node.has-children-label {
  cursor: pointer;
}
.department-outer
  .view-box
  .org-tree-wrapper
  .org-tree-node-label
  .org-tree-node-label-inner
  .custom-org-node
  .context-menu {
  position: absolute;
  right: -10px;
  bottom: 20px;
  z-index: 10;
}
</style>



